
import './TestimonialsContentStyle.css'
const TestimonialsContent = ({title,subTitle,img})=>{
    return(

        <>
        
              
           <div className='TestimonialsContent' >
           <div >
                    <div className='img'>
                        <img src={img} alt="" />
                    </div>
                    <p className='title'>
                        {title}
                    </p>
                    <h5 className='sub-title'>
                        {subTitle}
                    </h5>
              </div>
           </div>
       
        
        </>
    )
}

export default TestimonialsContent