

import './PricingContentStyle.css'


const PricingContent = ({title,subTitle,icone,price,list,color})=>{
    return(
        <>
   
              <div className='pricing-contanier' >
                 <div className={(color)?'pricing-card':'Color'} >
                 <h4 className='title'>
                    {title}
                </h4>
                <h4 className='price' >
                    {price}
                </h4>
                <h5 className='sub-title'  >
                   {subTitle}
                </h5>
                <ol>
                   <li>
                      {icone} <span>{list}</span>
                   </li>
                   <li>
                      {icone} <span>{list}</span>
                   </li>
                   <li>
                      {icone} <span>{list}</span>
                   </li>
                   <li>
                      {icone} <span>{list}</span>
                   </li>
                </ol>
                 </div>
              </div>
        </>
    )
}
export default PricingContent