

import TestimonialsContent from '../TestimonialsContent/TestimonialsContent'
import './TestimonialsStyle.css'
import firtsImage from '../../assests/11.jpg'
import secondImage from '../../assests/22.jpg'
import ThirdIamge from '../../assests/33.jpg'



const Testimonials =()=>{
    const TestimonialsContentData= [{
        id:1,
        img:firtsImage,
        title:'Rayan Hnide',
        subTitle:'Front-End Web developer'
    },
    {
        id:2,
        img:secondImage,
        title:'Rama Al Jouhary',
      
        subTitle:'Ui/UX Designer'
    },
    {
        id:3,
        img:ThirdIamge,
        title:'Mahdi Al-theeb',
        subTitle:'Founder'
       
       
    },]
    return(
        <>
         
         <div className="testiomnials">
            <h1 className="title">
                 Testiomnials
            </h1>
            <h2 className='sub-title'>
                Customer's Review
            </h2>
         </div>
               
         <div className='TestimonialsContentData'>
                       
                        {
                            TestimonialsContentData.map((e,i)=>{
                                return(
                                    <>
                                           
                                    <TestimonialsContent key={i} img={e.img} title={e.title} subTitle={e.subTitle} />
                                    
                                    </>
                                )
                            })
                        }
                        </div>
          
        
        </>
    )
}


export default Testimonials