import './HeroStyle.css'


const Hero = ({title,subTitle,description,contentBtn})=>{
    return(
        <>
         
              <div className='hero'>
            
                 <div className='content-hero' >
                    <h1 className='hero-title'>
                   {title}
                    </h1>

                    <h3 className='hero-sub-title'>
                    {subTitle}
                    </h3>

                    <p className='hero-description'>
                    {description}
                    </p>

                   
                    <div className='hero-btn-div'>
                        <button className='hero-btn'>{contentBtn}</button>
                    </div>
                    
                 </div>
            
               </div>
              
        
        
        </>
    )
}

export default Hero