import PricingContent from '../PricingContent/PricingContent'
import './PricingStyle.css'
import CheckIcon from '@mui/icons-material/Check';


const Pricing =()=>{

    const pricingContentData= [{
       title:'FREE',
       price:'200$/mo',
       subTitle:'Best for individual',
       icone :<CheckIcon sx={{color:'#04A9F4'}}/>,
       list:'lorem ipusem lorem  ',
       color:true
    },
    {
        title:'PREMIUM',
        price:'20$/mo',
        subTitle:'Best for small teams',
        icone :<CheckIcon sx={{color:'#04A9F4'}}/>,
        list:'lorem ipusem lorem  ',

        color:false
     },
     {
        title:'ENTERPRICE',
        price:'80$/mo',
        subTitle:'Best for industry leader',
        icone :<CheckIcon sx={{color:'#04A9F4'}}/>,
        list:'lorem ipusem lorem  ',
        color:true
     },
 
    ]
  
    return(


        <>
        
              <div className='pricing'>
                 <h1 className='title'>
                     Pricing
                 </h1>
                 <h2 className='sub-title'>
                    Flexiple Plans
                 </h2>
              </div>

                <div className='pricing-content'>
                      {
                        pricingContentData.map((e,i)=>{
                            return(

                                <PricingContent key={i} price={e.price}  list={e.list} icone={e.icone} subTitle={e.subTitle} icon={e.icone} title={e.title} color={e.color} />
                                
                                 )
                        })
                      }
                </div>
        
        </>
    )
}

export default Pricing