import './FooterStyle.css'

const Footer = () =>{
  const footerData=[{
    icons:['fa-brands fa-facebook','fa-brands fa-whatsapp','fa-brands fa-facebook','fa-brands fa-facebook','fa-brands fa-facebook',],
    list:['Home','About','Contact','Our Teams','View',]

  }]
    return(
        <>
        
          <div className="footer">
            <div className='logo'>
               <i className='fa fa-asl-interpreting'></i>
               <span className='logoName'> interpreting</span>
            </div>
          <div className='footer-container'>
          <div className='social-icons'>
            
              {
                footerData.map((e)=>{
                  
                  return(
                    <>
                      {
                        e.icons.map((icon)=>{
                          return(<><a href=""> <li className={icon}></li></a>  </>)
                        })
                      }
                    </>
                  )
                })
              }
            
        

     
             
          </div>
          <div className="footer-nav">
            <ul>
            {
            footerData.map((e)=>{
              return(<>
                 {
                  e.list.map((list)=>{
                    return(
                      <>
                       <li className='li'><a href="">{list}</a></li>
                      </>
                    )
                  })
                 }
              </>)
            })
           }
              
            </ul>
          </div>
          <div className="footer-bottom">
            <p>Copyright &copy; 2023 .Design By <span className='designer'>Rayan Hnide</span></p>
          </div>
        </div>
          </div>
        </>
    )
}
export default Footer