import './FeaturesContentStyle.css'

const FeaturesContent = ({img,description,revirse,title})=>{
    return(
        <>
           <div className='main'>
           <div className={(revirse)?'features-content':'revirse'}>
                <div className='row' >
                    <div>
                    <h2 style={{textAlign:'center'}} >{title}</h2>
                    <p >{description}</p>
                    </div>
                </div>
                
                <div style={{padding:"90px"}}><img style={{width:'100%'}} src={img} alt="" /></div>
             </div>
           </div>
        </>
    )
}
export default FeaturesContent